import React from 'react';
import { useLocation} from "react-router-dom";

import Logo  from '../../../assets/images/logo.svg';
import QuickStartBrand from './quick-start-brand.component';
import QuickStartPublisher from './quick-start-publisher.component';

import './quick-start.styles.scss';

const QuickStart = () => {
    function useQuery() {
        const { search } = useLocation();
        
        return React.useMemo(() => new URLSearchParams(search), [search]);
    }
    let query = useQuery();

    return ( 
        <div className="quick-start-wrapper">
            <div className="quick-start text-center">
                <div className="logo">
                    <img src={Logo} alt="" />
                </div>

                {
                    query.get("user") == "publisher" ? <QuickStartPublisher /> : 
                    query.get("user") == "brand" ? <QuickStartBrand /> : ""
                }                
            </div>
        </div>

    );
}
 
export default QuickStart;