import React, { useState, useEffect, useRef } from 'react';

const Dropdown = () => {
    const [showDropdown, setShowDropdown] = useState(false);
    const [category, setCategory] = useState('Select Category');  
    
    const categoryList =["Browser", "OEM", "Utility Apps", "Games", "Fintech"];

    const categorySetter = (e) => {
      setCategory(e);
      setShowDropdown(false);
    }

    function useOutside(ref) {
      useEffect(() => {
        function handleClickOutside(event) {
          if (ref.current && !ref.current.contains(event.target)) {
            setShowDropdown(false);              
          }
        }
  
        document.addEventListener("mousedown", handleClickOutside);
        return () => {
          document.removeEventListener("mousedown", handleClickOutside);
        };
      }, [ref]);
    }

    const wrapperRef = useRef(null);
    useOutside(wrapperRef);

    return ( 
        <div className="position-relative">
            <label htmlFor="category">Select Category</label>
            <div className={`dropdown-execute ${showDropdown ? "arrow-up": "arrow-down"}`} onClick={() => setShowDropdown(!showDropdown)} ref={wrapperRef}>{category}</div>            
            <div className={`form-dropdown custom-scroll ${showDropdown ? "show" : ""}`}>   
            <ul ref={wrapperRef}>
                {
                    categoryList.map(categoryName => <li key={categoryName} onClick={() => categorySetter(categoryName)}>{categoryName}</li>)                          
                }
            </ul>
            </div>   
        </div>
     );
}
 
export default Dropdown;