import Logo from '../../assets/images/logo-home.svg';
import NotificationIcon from '../../assets/images/notification.svg';
import ProfileIcon from '../../assets/images/profile.svg';

import './marketplace.styles.scss';

const MarketPlace = () => {
    return ( 
        <header className="d-flex d-justify-between align-items-center">
            <div className="position-relative d-flex">
                <div className="logo mr20">
                    <img src={Logo} alt="Logo" />
                </div>

                <nav>
                    <a href="">Reports</a>
                    <a href="">Markerplace</a>
                    <a href="">Deals</a>
                    <a href="">Packages</a>
                </nav>
            </div>

            <div className="header-utility d-flex">
                <a href="/login">Login</a>
                <div className="notification mr10">
                    <div className="notification-icon">
                        <img src={NotificationIcon} alt="Notification" />
                    </div>
                </div>

                <div className="profile">
                    <div className="profile-icon">
                        <img src={ProfileIcon} alt="Profile" /> <i></i>
                    </div>
                </div>
            </div>
        </header>
    );
}
 
export default MarketPlace;