import React, { Component } from 'react';
import AffinityLogo from '../../assets/images/affinity-logo.png';

import './footer.styles.scss';
import { Link } from 'react-router-dom';

const Footer = () => {
    return ( 
        <footer className="footer d-flex d-flex-column align-items-center">
            <ul className='d-flex mb10'>
                <li>
                    <Link to='#'>Privacy Policy</Link>
                </li>
                <li>
                    <Link to='#'>Terms of Use</Link>
                </li>
                <li>
                    <Link to='#'>Cookie Settings</Link>
                </li>
                <li>VEVE © 2023 All Rights Reserved</li>
            </ul>

            <div className="affinity-venture d-flex align-items-center">
                An <img src={AffinityLogo} alt='Affinity' /> Venture
            </div>
        </footer>
     );
}
 
export default Footer;

