import React from 'react';
import Frame1 from '../../../assets/images/auth/quick-start/brand/frame-1.svg';
import Frame2 from '../../../assets/images/auth/quick-start/publisher/frame-2.svg';
import Frame3 from '../../../assets/images/auth/quick-start/publisher/frame-4.svg';
import Frame4 from '../../../assets/images/auth/quick-start/brand/frame-4.svg';

import Buttons from '../../form-components/buttons';
import { useState } from 'react';

const QuickStartBrand = () => {
    const [count, setCount] = useState(0);
    const [countLeft, setCountLeft] = useState(0);
    const quickStart = [
        {
            id: 1,
            image: Frame1,
            paragraph: "Discover Publisher beyond search and social"
        },
        {
            id: 2,
            image: Frame2,
            paragraph: "Negotiate best deals directly from publisher with 100% transparency"
        },
        {
            id: 3,
            image: Frame3,
            paragraph: "Track the delivery of the app"
        },
        {
            id: 4,
            image: Frame4,
            paragraph: "List your app on OEM app stores",
            tagline: "Start your 1st campaign now!"
        }
    ];
    const quickStartWidth = quickStart.length * 420;
    
    const setCountIncreament = () => {
        setCount(count + 1);
        setCountLeft((-(count + 1) * 420) + 'px'); 
    }

    const setCountdecreament = () => {
        setCount(count - 1);
        setCountLeft((-(count - 1) * 420) + 'px'); 
    }

    return ( 
        <React.Fragment>
            <div className="slider quick-start-items">
                <div className="slider-items-container">
                    <div className="slider-items" style={{width: quickStartWidth + 'px', left: countLeft }}>
                        {
                            quickStart.map((slide, index) => {
                                return (
                                    <div className={`quick-start-item slide${index}`} key={slide.id}>
                                        <img src={slide.image} alt='' />
                                        <p>
                                            {slide.paragraph}
                                            {
                                                slide.tagline ? <b className='d-block mt10'>{slide.tagline}</b> : ""
                                            }
                                        </p>
                                    </div>
                                )
                            })
                        }
                    </div>
                </div>

                <div className="slider-counters">
                    {
                        quickStart.map((slidecount, index) => <div  key={slidecount.id} className={`slider-counter ${index == count ? 'active': ''}`}></div>)
                    }
                </div>

                <div className={`slider-next slider-action ${count == (quickStart.length - 1) ? 'hide': 'show'}`} onClick={setCountIncreament}></div>
                <div className={`slider-previous slider-action ${count == 0 ? 'hide': 'show'}`} onClick={setCountdecreament}></div>
            </div>

            <a href='#' className='btn btn-primary d-block'>Get Started</a>
        </React.Fragment>
     );
}
 
export default QuickStartBrand;