import React from "react";

const Radio = (props) => { 
    return ( 
        <React.Fragment>
            <label className="radio-box">
                <input type="radio" name={props.name} id={props.id} value={props.value} onChange={props.onHandleClick} checked={props.checked} />
                <span className="radio-layer" htmlFor={props.id}></span>
                <span className="value" htmlFor={props.id}>{props.value}</span>
            </label>            
        </React.Fragment>
     );
}

export default Radio ;