import React, { useState } from "react";
import Logo from "../../../assets/images/logo.svg";
import Radio from "../../form-components/radio";
import { Link } from "react-router-dom";

import "../authentication.styles.scss";
import AuthAppPreview from "../auth-app-preview/auth-app-preview.component";
import Footer from "../../footer/footer.component";
import ContactPublisher from "./contact-publisher.component";
import ContactBrand from "./contact-brand.component";

const Contact = () => {
  const [contact, setContact] = useState("publisher");
  const [radio, setRadio] = useState("");

  const inputStyle = {
    fontSize: "16px",
    margin: "-20px 0 50px",
  };

  return (
    <React.Fragment>
      <div className="auth-container d-flex d-justify-between">
        <div className="auth-components d-flex d-flex-column d-justify-center">
          <div className="logo">
            <img src={Logo} alt="" />
          </div>

          <h1 style={inputStyle}>
            Fill the form below to apply for your username and password
          </h1>

          <div className="auth-login custom-scroll auth-form-scroll">
            {/* <div className={`error-message text-center ${props.error_show}`}>{props.error}</div> */}

            <form action="" id="contact">
              <div className="form-control">
                <label htmlFor="">Choose the type of user</label>
                <div className="d-flex">
                  <Radio
                    name={"user"}
                    id={"publisher"}
                    checked={contact == "publisher" ? "checked" : ""}
                    value={"publisher"}
                    onHandleClick={() => setContact("publisher")}
                  />
                  <Radio
                    name={"user"}
                    id={"brand"}
                    checked={contact == "brand" ? "checked" : ""}
                    value={"brand"}
                    onHandleClick={() => setContact("brand")}
                  />
                </div>
              </div>

                {
                    contact == "publisher" ? <ContactPublisher /> : 
                    contact == "brand" ? <ContactBrand /> : "" 
                }

              <div className="or-block mb20 mt20">OR</div>

              <div className="text-center">
                <span className="d-block font-12">write to us</span>
                <Link to="mailto:sales@veve.com">sales@veve.com</Link>
              </div>
            </form>
          </div>
        </div>

        <AuthAppPreview></AuthAppPreview>
      </div>
      <Footer></Footer>
    </React.Fragment>
  );
};

export default Contact;
