import React, { Component } from 'react';
import FormInput from '../../form-components/form-input';
import Buttons from '../../form-components/buttons';
import { Link } from 'react-router-dom';

const LoginWithUsername = (props) => {    
    return ( 
        <React.Fragment>
            <form action="" id='sign_in_username'>
                <FormInput label={'Username'} id={'username'} inputType={'text'} placeholder={'Enter Username'} />

                <FormInput label={'Password'} id={'password'} inputType={'password'} placeholder={'Enter Password'} />

                <Buttons className={'btn btn-disabled'} disabled>Sign In</Buttons>

                <div className='text-right'>
                    <Link onClick={props.handleLinkRedirect}>Use OTP to login</Link>
                </div>
            </form>
        </React.Fragment>
     );
}
 
export default LoginWithUsername;
