const FormInput = (props) => {
    function IsPhone() {
        return <span className='is-phone'>+91 <i></i></span> 
    }

    function CheckIsPhone() {
        const checkIsPhone = props.checkIsPhone;
        if(checkIsPhone) {
            return <IsPhone />
        }
    }

    return ( 
        <div className={`form-control ${props.phone ? props.phone : ""}`}>
            <label htmlFor={props.id} className='d-block'>{props.label}</label>
            <CheckIsPhone checkIsPhone={(props.checkIsPhone) ? props.checkIsPhone : ""} />
            <input type={props.inputType} id={props.id} placeholder={props.placeholder} onChange={props.handleIsPhoneNumber} />
        </div>    
     );
}
 
export default FormInput;